 const validateName = (name) =>{
  name = name.replace(/\s+/g, ' ');
  name = name.trim().toLowerCase();
  let split = name.split(" ");
for(let i=0;i<split.length;i++)
{
    split[i] = split[i].charAt(0).toUpperCase() + split[i].slice(1);
}
return split.join(" ")
 }
